import React, { useEffect, useState } from 'react';
import axios from 'axios';
import { Link } from 'react-router-dom';
import BasicLayout from "../../layout/BasicLayout"; // Link Ïª¥Ìè¨ÎÑåÌä∏ Ï∂îÍ∞Ä
import { useDispatch } from 'react-redux'
import { setPageRequest, setResponseData } from "../../components/slice/store";


function BoardList() {
    const [pageRequest, setPageRequest] = useState({
        size: 10,
        type: '',
        location: '',
        keyword: '',
    });
    const [responseData, setResponseData] = useState({
        dtoList: [],
        prev: false,
        next: false,
        start: 0,
        end: 0,
        page: 1,
    });
    const dispatch = useDispatch();

    useEffect(() => {
        // Îç∞Ïù¥ÌÑ∞Î•º Î∞õÏïÑÏò§Îäî Ìï®Ïàò
        const fetchData = async () => {
            try {
                const response = await axios.get('http://localhost:8080/api/board/list', {
                    params: pageRequest,
                });
                setResponseData(response.data);
                dispatch(setPageRequest(pageRequest));
                dispatch(setResponseData(responseData));
            } catch (error) {
                console.log(error);
            }
        };

        fetchData();
    }, [pageRequest]);

    // ÌéòÏù¥ÏßÄ Î≥ÄÍ≤Ω Ìï®Ïàò
    const handlePageChange = (pageNum) => {
        setPageRequest((prev) => ({
            ...prev,
            page: pageNum,
        }));
    };

    return (
        <BasicLayout>
        <div>
            <div className="row mt-3">
                <form
                    onSubmit={(e) => {
                        e.preventDefault();
                        // Îç∞Ïù¥ÌÑ∞ ÏöîÏ≤≠ÏùÑ ÏúÑÌï¥ ÌéòÏù¥ÏßÄ ÏöîÏ≤≠ Ï†ïÎ≥¥Î•º ÏóÖÎç∞Ïù¥Ìä∏
                        // (ÌòÑÏû¨ form submitÏùÄ Î≥ÑÎèÑÏùò ÎèôÏûëÏùÑ ÌïòÏßÄ ÏïäÏùå, ÌïÑÏöîÏóê Îî∞Îùº Ï∂îÍ∞Ä Íµ¨ÌòÑ Í∞ÄÎä•)
                    }}
                >
                    <div className="col">
                        <input type="hidden" name="size" value={pageRequest.size} />
                        <div className="input-group">
                            <select
                                className="form-select"
                                name="type"
                                value={pageRequest.type}
                                onChange={(e) => setPageRequest({ ...pageRequest, type: e.target.value })}
                            >
                                <option value="">---</option>
                                <option value="t">Ï†úÎ™©</option>
                                <option value="c">ÎÇ¥Ïö©</option>
                                <option value="w">ÏûëÏÑ±Ïûê</option>
                                <option value="tc">Ï†úÎ™© ÎÇ¥Ïö©</option>
                                <option value="tcw">Ï†úÎ™© ÎÇ¥Ïö© ÏûëÏÑ±Ïûê</option>
                            </select>

                            <select
                                className="form-select"
                                name="location"
                                value={pageRequest.location}
                                onChange={(e) => setPageRequest({ ...pageRequest, location: e.target.value })}
                            >
                                <option value="">---</option>
                                <option value="seoul">ÏÑúÏö∏</option>
                                <option value="gg">Í≤ΩÍ∏∞ÎèÑ</option>
                                <option value="gwd">Í∞ïÏõêÎèÑ</option>
                                {/* Îã§Î•∏ ÏßÄÏó≠ ÏÑ†ÌÉù */}
                            </select>

                            <input
                                type="text"
                                className="form-control"
                                name="keyword"
                                value={pageRequest.keyword}
                                onChange={(e) => setPageRequest({ ...pageRequest, keyword: e.target.value })}
                            />

                            <div className="input-group-append">
                                <button className="btn btn-outline-secondary" type="submit">
                                    Search
                                </button>
                                <button
                                    className="btn btn-outline-secondary"
                                    type="button"
                                    onClick={() => setPageRequest({ size: 10, type: '', location: '', keyword: '' })}
                                >
                                    Clear
                                </button>
                            </div>
                        </div>
                    </div>
                </form>
            </div>

            <div className="row mt-3">
                <div className="col">
                    <div className="card">
                        <div className="card-header">Î¶¨Î∑∞ Î™©Î°ù</div>
                        <div className="card-body">
                            <h5 className="card-title">Î¶¨Î∑∞ Î™©Î°ù</h5>
                            <table className="table">
                                <thead>
                                <tr>
                                    <th scope="col">Í≤åÏãúÎ¨º Î≤àÌò∏</th>
                                    <th scope="col">Ï†úÎ™©</th>
                                    <th scope="col">Í≤åÏãúÏûê</th>
                                    <th scope="col">Îì±Î°ùÎÇ†Ïßú</th>
                                </tr>
                                </thead>
                                <tbody>
                                {responseData.dtoList.map((dto) => (
                                    <tr key={dto.bno}>
                                        <td>{dto.bno}</td>
                                        <td>
                                            {/* Link Ïª¥Ìè¨ÎÑåÌä∏ ÏÇ¨Ïö© */}
                                            <Link to={`/board/read?bno=${dto.bno}`} className="text-decoration-none">
                                                {dto.title}
                                            </Link>
                                            <span className="badge" style={{ backgroundColor: '#0a53be' }}>
                                                {dto.replyCount}
                                            </span>
                                        </td>
                                        <td>{dto.writer}</td>
                                        <td>{new Date(dto.regDate).toLocaleDateString()}</td>
                                    </tr>
                                ))}
                                </tbody>
                            </table>

                            <div className="float-end">
                                <ul className="pagination flex-wrap">
                                    {responseData.prev && (
                                        <li className="page-item">
                                            <button
                                                className="page-link"
                                                onClick={() => handlePageChange(responseData.start - 1)}
                                            >
                                                Previous
                                            </button>
                                        </li>
                                    )}
                                    {Array.from({ length: responseData.end - responseData.start + 1 }).map(
                                        (_, index) => (
                                            <li
                                                className={`page-item ${
                                                    responseData.page === responseData.start + index ? 'active' : ''
                                                }`}
                                                key={index}
                                            >
                                                <button
                                                    className="page-link"
                                                    onClick={() => handlePageChange(responseData.start + index)}
                                                >
                                                    {responseData.start + index}
                                                </button>
                                            </li>
                                        )
                                    )}
                                    {responseData.next && (
                                        <li className="page-item">
                                            <button
                                                className="page-link"
                                                onClick={() => handlePageChange(responseData.end + 1)}
                                            >
                                                Next
                                            </button>
                                        </li>
                                    )}
                                </ul>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        </BasicLayout>
    );
}

export default BoardList;
